  .HomeStyle_my_animated_para__PNXNX {
    border-right: solid 3px rgba(0,255,0,.75);
    white-space: nowrap;
    overflow: hidden;    
    font-family: 'Source Code Pro', monospace;  
    font-size: 28px;
    color: rgba(255,255,255,.70);
    margin: 0 auto;
  }

  /* Animation */
  .HomeStyle_my_animated_para__PNXNX {
    animation: HomeStyle_animated-text__EcwSm 4s steps(29,end) 1s 1 normal both,
               HomeStyle_animated-cursor__2SYY2 600ms steps(29,end) infinite;
  }

  /* text animation */

  @keyframes HomeStyle_animated-text__EcwSm{
    from{width: 0;}
    to{width: 472px;}
  }

  /* cursor animations */

  @keyframes HomeStyle_animated-cursor__2SYY2{
    from{border-right-color: rgba(0,255,0,.75);}
    to{border-right-color: transparent;}
  }



